
@font-face {
  font-family: 'coolvetica';
  src: url("./assets/fonts/coolvetica.eot") format("embedded-opentype"), url("./assets/fonts/coolvetica.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
button {
  font-family: "coolvetica", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: 1.5px;
}
a {
  text-decoration: none;
}
h1, h2 {
  margin-top: 0;
  text-transform: uppercase;
  font-weight: lighter;
}
li, div {
  font-size: 1em;
}
p {
  font-size: 1.25em;
  margin: 24px 0;
}
body {
  min-width: 440px;
  background-color: #f3f6ff;
}
canvas {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 101;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
}
@-webkit-keyframes fadein {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fadein {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
.app-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  opacity: 1.0;
}
.transition-wrap {
  background-color: #595F7F;
  border-top: 3px solid #8c21ff;
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
}
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /* IE10+ CSS styles go here */
.transition-wrap {
    margin-top: -4px;
}
}
.content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.white-background-wrap {
  width: 100%;
  background-color: #f3f6ff;
}
.slide-right-enter {
  -webkit-transform: translateX(15px);
          transform: translateX(15px);
}
.slide-right-enter-active {
  transition: all .7s ease;
}
.slide-right-leave-active {
  -webkit-transform: translateX(-15px);
          transform: translateX(-15px);
  transition: all 0.7s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-left-enter {
  -webkit-transform: translateX(-15px);
          transform: translateX(-15px);
}
.slide-left-enter-active {
  transition: all .7s ease;
}
.slide-left-leave-active {
  -webkit-transform: translateX(15px);
          transform: translateX(15px);
  transition: all 0.7s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-right-enter {
  -webkit-transform: translateX(15px);
          transform: translateX(15px);
  opacity: 0;
}
.slide-fade-right-enter-active {
  transition: all 300ms ease;
}
.slide-fade-right-leave-active {
  -webkit-transform: translateX(-15px);
          transform: translateX(-15px);
  transition: all 300ms cubic-bezier(1, 0.5, 0.8, 1);
  opacity: 0;
}
.slide-fade-left-enter {
  -webkit-transform: translateX(-15px);
          transform: translateX(-15px);
  opacity: 0;
}
.slide-fade-left-enter-active {
  transition: all 300ms ease;
}
.slide-fade-left-leave-active {
  -webkit-transform: translateX(15px);
          transform: translateX(15px);
  transition: all 300ms cubic-bezier(1, 0.5, 0.8, 1);
  opacity: 0;
}
.fade-enter-active {
  transition: all .3s ease;
}
.fade-leave-active {
  transition: all 0.7s cubic-bezier(1, 0.5, 0.8, 1);
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.fadeOut-enter-active {
  transition: all .7s ease;
}
.fadeOut-enter, .fadeOut-leave-active {
  opacity: 0;
}
.fade-down-enter-active {
  transition: all .3s ease;
}
.fade-down-leave-active {
  transition: all 0.7s cubic-bezier(1, 0.5, 0.8, 1);
}
.fade-down-enter, .fade-down-leave-active {
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
  opacity: 0;
}
.section-header {
  display: inline-block;
  border-radius: 25px;
  padding: 6px 24px;
  color: #f3f6ff;
  background-color: #8c21ff;
}
#app {
  font-family: "coolvetica", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  letter-spacing: 1px;
  color: #585a61;
  font-size: .9em;
  min-height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.link-btn {
  border: 2px solid #8c21ff;
  background-color: rgba(47, 50, 67, 0.75);
  border-radius: 25px;
  padding: 6px 18px;
  color: #f3f6ff;
  transition: all 0.1s ease-in-out;
  text-decoration: none;
}
.link-btn:hover {
    background-color: #8c21ff;
    cursor: pointer;
}
.link-btn:focus {
    background-color: #8c21ff;
}



