
.skipToMain {
  position: absolute;
  left: -99999px;
  top: 0;
  margin-left: 0;
  text-align: center;
  list-style: outside none;
  background: #2f3243;
  color: #f3f6ff;
  border: none;
}
.skipToMain:focus {
    position: relative;
    display: inline-block;
    background: #2f3243;
    left: 0;
}
.nav {
  text-transform: uppercase;
}
.nav h1 {
    color: #f3f6ff;
    margin: 0 0 12px 0;
}
.nav .logo {
    width: 200px;
    transition: all 0.1s ease-in-out;
}
.nav-top {
    position: absolute;
    top: 5px;
    left: 0;
    right: 0;
    z-index: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    width: 90%;
    max-width: 1500px;
    margin: 0 auto;
}
.nav-top .social {
      transition: all 0.1s ease-in-out;
      display: inline-block;
      margin: 24px 0;
      white-space: nowrap;
}
.nav-top a:hover img {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
}
.nav-mid {
    height: 34.5vw;
    min-height: 175px;
    background: #2D3040;
    overflow: hidden;
    position: relative;
}
.nav-mid .image-wrap img {
      min-width: 440px;
      width: 100%;
}
.nav-mid .centered {
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
}
.nav-mid .centered h1 {
        font-size: 2vw;
        text-shadow: 2px 2px 10px #2f3243;
}
.nav-mid .centered .logo-sm {
        position: relative;
        width: 5vw;
        height: 5vw;
        min-width: 70px;
        min-height: 70px;
        max-height: 100px;
        margin: 6px auto;
}
.nav-mid .centered .logo-sm img {
          position: absolute;
          width: 100%;
          left: 0;
          top: 0;
          border: 2px solid #8c21ff;
          border-radius: 70px;
          box-shadow: 0 0 0 #8c21ff;
          opacity: 1;
          cursor: pointer;
}
.nav-mid .centered .logo-sm img.gold {
            border: 2px solid #8c21ff;
            opacity: 0;
            transition: all 30000ms ease-in, box-shadow 250ms ease-in-out, visibility 0ms ease-in-out, -webkit-transform 750ms ease-in-out;
            transition: all 30000ms ease-in, box-shadow 250ms ease-in-out, transform 750ms ease-in-out, visibility 0ms ease-in-out;
            transition: all 30000ms ease-in, box-shadow 250ms ease-in-out, transform 750ms ease-in-out, visibility 0ms ease-in-out, -webkit-transform 750ms ease-in-out;
}
.nav-mid .centered .logo-sm img.gold:hover, .nav-mid .centered .logo-sm img.gold:focus {
              box-shadow: 0 0 25px #8c21ff;
              -webkit-transform: scale(1.05);
                      transform: scale(1.05);
}
.nav-mid .centered .logo-sm img.gold.loaded {
              opacity: 1;
}
.nav-mid .centered .logo-sm img.loaded {
            opacity: 0;
}
.nav-bar {
    background-color: #2f3243;
    white-space: nowrap;
}
.nav-bar .icon {
      display: block;
      visibility: hidden;
      margin: 0 auto;
      fill: #f3f6ff;
}
.nav-bar .external {
      margin: 0 5px;
      fill: #f3f6ff;
      vertical-align: center;
      width: 0.75em;
      height: 0.75em;
}
.nav-bar a {
      display: -webkit-inline-box;
      display: -ms-inline-flexbox;
      display: inline-flex;
      position: relative;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      padding: 36px 3px 36px 5px;
      color: #f3f6ff;
      min-width: 100px;
      transition: all 0.1s ease-in-out;
      transition-delay: 25ms;
      margin: 0 1px 0 0;
}
.nav-bar a .nav-link-wrap {
        padding-bottom: 20px;
}
.nav-bar a ~ a .nav-link-wrap:before {
        content: "";
        position: absolute;
        left: -1px;
        background: #1e1e1e;
        height: 48px;
        width: 1px;
}
.nav-bar a:hover, .nav-bar a:focus {
        background-color: #8c21ff;
}
.nav-bar .router-link-active {
      background-color: #8c21ff;
}
.nav-bar .router-link-active a {
        color: #f3f6ff;
}
.nav-bar .router-link-active .icon {
        visibility: visible;
}
.nav-bar .highlighted {
      background-color: #8c21ff;
}
.nav.small {
    background-color: #2f3243;
}
.nav.small .nav-top {
      position: inherit;
}
.return {
  border: 2px solid #8c21ff;
  background: none;
  border-radius: 25px;
  transition: all 0.1s ease-in-out;
  font-family: inherit;
  letter-spacing: inherit;
  color: #f3f6ff;
  padding: 8px 12px;
}
.return:hover, .return:focus, .return.selected {
    cursor: pointer;
    background-color: #8c21ff;
}
.return-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    max-width: 57em;
    margin: -12px auto 12px auto;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    color: #f3f6ff;
}
.return svg {
    vertical-align: text-bottom;
}
@media (max-width: 780px) {
.nav-top {
    text-align: center;
    width: 97%;
}
.nav-top .social {
      margin-top: 12px;
}
.nav-top .logo {
      width: 150px;
}
.nav-bar a {
    min-width: 0;
    padding: 12px 1em;
}
.nav-link-wrap {
    font-size: smaller;
    font-weight: lighter;
}
.nav-mid .centered {
    width: 100%;
    margin-top: -10px;
}
.nav-mid .centered .logo-sm {
      visibility: hidden;
}
}
@media (max-width: 640px) {
.nav-bar a.store {
    display: none;
}
}
@media (max-width: 555px) {
.nav-bar a {
    padding: 12px 0.45em;
}
}



